:root {
  --primary-color: #14bdac;
  --primary-color-light: #c8f9f4;
  --text-color: #063732;
}

* {
  box-sizing: border-box;
}

body {
  font: 14px 'Century Gothic', Futura, sans-serif;
  margin: 20px;
  background-color: var(--primary-color);
  height: 100vh;
  display: flex;
  justify-content: center;
  padding-top: 30px;
  color: var(--text-color);
}

ol,
ul {
  padding-left: 30px;
  padding-top: 0;
  margin-top: 0;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background-color: var(--primary-color);
  border: 2px solid var(--text-color);
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square.white {
  color: #fff;
}

.square:focus {
  outline: none;
}

.square:first-of-type {
  border-left-color: transparent;
}

.square:last-of-type {
  border-right-color: transparent;
}

.board-row.ceil .square {
  border-top-color: transparent;
}

.board-row.floor .square {
  border-bottom-color: transparent;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: column;
}

.game-info {
  margin-left: 20px;
}

main {
  display: flex;
}

li button {
  border: 2px solid transparent;
  border-radius: 8px;
  margin-bottom: 4px;
}

button {
  outline: none;
}
